.afu-flex {
  display: flex;
}
.afu-flex--centralize {
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}
.afu-flex--flexed {
  flex: 1;
}
.afu-flex--row {
  flex-direction: row;
}
.afu-helper-block {
  display: block;
}
.afu--hidden {
  display: none;
  visibility: hidden;
}
.afu--text-center {
  text-align: center;
}



